@import './../../utils/font.scss';
.UserInfoWrap{
    height: 100%;

    .UserInfoMain{
        min-height: 100%;
        background: rgb(49,12,29);
    }

    // 头部部分
    .UIHeaderWrap{
        display: block;
        width: 100%;
        position: relative;
        .UIHeader{
            width: 100%;
            height: 50px;
            background-color: rgb(81,31,40);
            position: relative;
            text-align: center;
            line-height: 50px;

            .iconfont{
                position: absolute;
                top: 0%;
                left: 10%;
                color:#fff;
            }

            span{
                display: inline-block;
                height: 100%;
                font-size: 22px;
                color:#fff;
                font-weight: bold;
            }
        }
    }


    // 信息填写部分
    .UIMainWrap{
        width: 100%;
        position: relative;

        .UIMain{
            width: 100%;
            position: relative;

            .InfoForm{
                position: relative;
                width: 100%;
    
                form{
                    input{
                        height: 100%;
                        background:transparent;
                        outline: none;
                        border-color: transparent;
                        color: white;
                        font-size: 16px;
                        margin-left: 5px;
                    }
                    label{
                        display: inline-block;
                        text-align: left;
                        width: 18%;
                        height: 100%;
                        line-height: 50px;
                        color: rgb(255,153,102);
                        font-size: 16px;
                        margin-left:22px;
                    }
    
                    .user , .InfoPhone{
                        display: flex;
                        width: 100%;
                        height: 50px;
                        // height: 1rem;
                        line-height: 1rem;
                        border-bottom: 2px solid rgb(49,12,29);
                    }
                    .AddressInfo{
                        border-bottom: 2px solid rgb(49,12,29);
                    }
                    .InfoPay{
                        width: 100%;
                        height: 8vh;
                        line-height: 6.9vh;
    
                        span{
                            font-size: 2vh;
                            color: #fff;
                        }
                        .OldP{
                            text-decoration: line-through;
                            color: #333;
                        }
                    }
    
                    .InfoAddress , .AddressInfo {
                        width: 100%;
                        height: 50px;
                        // height: 6rem;
                        line-height: 1.3rem;

                        p{
                            display: inline-block;
                            // background: red;
                            font-size: 16px;
                            width: 70%;
                            height: 100%;
                            margin-left: 5px;
                        }
                    }
                }
            }
        }
    }

    // 确定按钮
    .InfoFooter{
        width: 68%;
        height: 50px;
        margin-top: 30px;
        margin-left: auto;
        margin-right: auto;
        border-radius: 50px;
        box-sizing: border-box;
        padding: 5px 5px;
        background:linear-gradient(to bottom , rgb(253,229,110) , rgb(253,143,1));
    
        .InfoB{
            width: 100%;
            height: 100%;
            background: linear-gradient(to right , rgb(252,215,38) , rgb(253,174,22));
            border-radius: 50px;
            text-align: center;
            line-height: 40px;

            span{
                color: #000;
                font-size: 18px;
                font-weight: bold;
            }
        }
    }
    .unInfoFooter{
        width: 68%;
        height: 50px;
        margin-top: 30px;
        margin-left: auto;
        margin-right: auto;
        border-radius: 50px;
        box-sizing: border-box;
        padding-bottom: 5px;
        background:linear-gradient(to bottom , rgb(128,128,128) , rgb(102,102,102));

        .InfoB{
            width: 100%;
            height: 100%;
            background: rgb(128,128,128);
            border-radius: 50px;
            text-align: center;
            line-height: 45px;

            span{
                color: #000;
                font-size: 18px;
                font-weight: bold;
            }
        }
    }

}