.Pos_Container{
    height: 100%;
    

    .Pos_Main{
        min-height: 100%;
        background:url(./../HomePage/images/HPBck.jpg) no-repeat  top;
        background-size: 100% ;


        //头部标题
        .PosHeader_Container{
            width: 100%;
            position: relative;
            overflow: hidden;


            .PosHeader_HeaderTit1{
                width: 100%;
                height: 59px;
                position: relative;
                top:0%;
                font-size: 20px;
                text-align: center;
                line-height: 80px;
                margin-top: 22px;


                .PosHeader_HeaderTit1Text{
                    color: rgb(252, 217, 119)
                }
                
            }
            .PosHeader_HeaderTit1::before{
                content: '';
                width: 10%;
                height: 3px;
                background: rgb(252, 217, 119);
                position: absolute;
                top: 64%;
                left: 9%;
            }
            .PosHeader_HeaderTit1::after{
                content: '';
                width: 10%;
                height: 3px;
                background: rgb(252, 217, 119);
                position: absolute;
                top: 64%;
                right: 9%;
            }

            .PosHeader_HeaderTit2{
                width: 80%;
                margin: 0 auto;
            }
        }

        //主体部分
        .PosUI_Container{
            width: 100%;
            position: relative;
            margin-top: 43px;

            //头像部分
            .PosUI_TitWrap{
                width: 100%;
                position: relative;

                .PosUI_AvaWrap{
                position: absolute;
                top: -48%;
                left: 42%;
                width: 60px;
                height: 60px;
                background: rgb(253,130,99);
                border-radius: 100%;

                img{
                    width: 82%;
                    margin-left: 6px;
                    margin-top: 5px;
                    border-radius: 100%;
                }
                
            }
                .PosUI_IdWrap{
                    position: absolute;
                    top: 33%;
                    left: 0%;
                    width: 100%;
                    height: 40px;
                    line-height: 40px;
                    text-align: center;


                    span{
                        font-size: 16px;
                        color: #fff;                
                    }
                }
            }

            //支付界面
            .PosUI_MainWrap{
                width: 85%;
                // height: 478px;
                margin: 0 auto;
                display: flex;
                margin-top: -3px;

                //左边框
                .PosUI_ItemL{
                    width: 3.4%;
                    height: 100%;
                    
                    img{
                        width:100%;
                        height: 100%;
                    }
                }
                //右边框
                .PosUI_ItemR{
                    width: 3.4%;
                    height: 100%;
                    
                    img{
                        width:100%;
                        height: 100%;
                    }
                }
                //中间内容
                .PosUI_ItemM{
                    width: 93.4%;
                    // height: 100%;
                    box-sizing: border-box;
                    padding: 18px 10px;
                    background-color: #fff;
                    background: url(./images/PayBck.png) no-repeat center center;
                    background-size: 100% 100%;

                    //运费样式
                    .PosUI_Price{
                        position: relative;
                        width: 78%;
                        margin-top: -9px;
                        margin-left: auto;
                        margin-right: auto;

                        img{
                            width: 100%;
                        }
                        p{  
                            position: absolute;
                            top: 34%;
                            left: 23%;
                            font-size: 25px;
                            font-weight: bold;
                            text-align: center;
                            color: rgb(249,49,11);
                        }
                    }

                    //支付按钮
                    .PosUI_BtnWrap{
                        width: 90%;
                        height: 60px;
                        background:linear-gradient(to bottom , rgb(253,130,100) , rgb(251,123,205));
                        box-sizing: border-box;
                        padding: 5px 5px;
                        border-radius: 50px;
                        margin-top: 14px;
                        margin-left: auto;
                        margin-right: auto;
                        
                        .PosUI_Btn{
                            width: 100%;
                            height: 100%;
                            background: linear-gradient(to right , rgb(247,48,13) , rgb(255,1,151));
                            border-radius: 50px;
                            text-align: center;

                            span{
                                text-align: center;
                                font-size: 20px;
                                line-height: 50px;
                                color: #fff;
                                font-weight: bold;
                            }
                        }
                    }
                }
            }

            //底部
            .PosUI_FooterWrap{
                width: 90%;
                height: 100px;
                background: linear-gradient(to bottom right , rgb(249,76,83) , rgb(231,0,0));
                margin-top: -2px;
                margin-left: auto;
                margin-right: auto;
                border-radius: 5px;
                z-index: 999;
                box-sizing: border-box;
                padding: 8px 8px ;
                overflow: hidden;

                .PosUI_FooterMain{
                    width: 100%;
                    height: 100%;
                    background: linear-gradient(to right , rgb(249,78,84) , rgb(247,49,12));
                    overflow: hidden;

                    .PosUI_FooterBtnWrap{
                        width: 80%;
                        height: 60px;
                        margin-top: 11px;
                        margin-right: auto;
                        margin-left: auto;
                        background: linear-gradient(to bottom , rgb(253,225,107) , rgb(253,143,3));
                        box-sizing: border-box;
                        padding: 5px 5px;
                        border-radius: 50px;
                        box-shadow: 0px 4px 23px #000;

                        .PosUI_FooterBtn{
                            width: 100%;
                            height: 100%;
                            background: linear-gradient(to right , rgb(252,215,38) , rgb(253,174,22));
                            border-radius: 50px;
                            text-align:center;

                            span{
                                line-height: 50px;
                                color: #000;
                                font-size: 20px;
                                font-weight: bold;
                            }
                        }

                    }
                
                
                }
            }
        }

        //底部标识
        .PosBotLogo_Container{
            width: 100%;
            position: relative;
            margin-top: 23px;
        }

    }   


    
}