.ActSuc_Container{
    height: 100%;
    .ActSuc_wrap{
        min-height: 100%;
        background:url(./../HomePage/images/HPBck.jpg) no-repeat top;
        background-size:100%;


        //标题
        .ActSuc_TitContainer{
            width: 100%;
            overflow: hidden;

            .ActSuc_TitWrap{
                width: 60%;
                margin-top: 23%;
                margin-left: auto;
                margin-right: auto;

                img{
                    width: 100%;
                }
            }
        }

        // 内容
        .ActSuc_ConContainer{
            width: 100%;

            .ActSuc_ConMain{
                width: 100%;
                position: relative;

                .ActSuc_Item1{
                    width: 76%;
                    margin-top: 20%;
                    position: relative;
                    margin-left: auto;
                    margin-right: auto;
                    height: 300px;
                    background-color: rgb(137,51,68);
                    border-radius: 17px;
                    box-sizing: border-box;
                    padding: 0px 0px;


                    .ActSuc_Item2{
                        width: 90%;
                        height: 200px;
                        position: absolute;
                        top: -16%;
                        left: 5%;
                        background:linear-gradient(to bottom , rgb(255,255,255) , rgb(179,179,179));
                        border-radius: 17px;

                        .ActSuc_Item2Tit{
                            width: 100%;
                            height: 30px;
                            line-height: 30px;
                            margin-top: 8%;
                            margin-left: auto;
                            margin-right: auto;
                            text-align: center;
                            color: rgb(247,50,78);
                            font-size: 20px;
                            font-weight: bold;
                        }
                        .ActSuc_Item2Img{
                            width: 100%;
                            height: 170px;
                            margin-top: -8%;
                            background: url(./../HomePage/images/HPProduct_Img.png) no-repeat center;
                            background-size: 100% ;
                        }
                    }
                }
                .ActSuc_Item3{
                    width: 76%;
                    height: 215px;
                    position: absolute;
                    top: 28%;
                    left: 12%;
                    background: url(./images/ASMain_ItemWrap.png) no-repeat center;
                    background-size: 100% 100%;

                    .ActSuc_Item3ShareWrap{
                        width: 81%;
                        height: 47px;
                        margin-top: 29%;
                        margin-left: auto;
                        margin-right: auto;
                        background: linear-gradient(to bottom , rgb(253,225,107) , rgb(253,143,3));
                        box-sizing: border-box;
                        padding: 5px 5px;
                        border-radius: 50px;
                        box-shadow: 0px 4px 23px #000;
                        z-index: 999999;


                        .ActSuc_Item3Share{
                            width: 100%;
                            height: 100%;
                            background: linear-gradient(to right , rgb(252,215,38) , rgb(253,174,22));
                            border-radius: 50px;
                            text-align:center;

                            span{
                                line-height: 37px;
                                color: #000;
                                font-size: 20px;
                                font-weight: bold;
                            }
                        }
                    }
                    .ActSuc_Item3Other{
                        width: 81%;
                        height: 47px;
                        margin-top:6%;
                        margin-left: auto;
                        margin-right: auto;
                        display: flex;
                        justify-content:space-between;

                        .ActSuc_Item3getOrderWrap{
                            width: 48%;
                            height: 100%;
                            background: linear-gradient(to bottom , rgb(253,225,107) , rgb(253,143,3));
                            box-sizing: border-box;
                            padding: 5px 5px;
                            border-radius: 50px;
                            box-shadow: 0px 4px 23px #000;

                            .ActSuc_Item3getOrder{
                                width: 100%;
                                height: 100%;
                                background: linear-gradient(to right , rgb(252,215,38) , rgb(253,174,22));
                                border-radius: 50px;
                                text-align:center;

                                span{
                                    line-height: 37px;
                                    color: #000;
                                    font-size: 18px;
                                    font-weight: bold;
                                }
                            }
                        }
                        .ActSuc_Item3BckHPWrap{
                            width: 48%;
                            height: 100%;
                            background: linear-gradient(to bottom , rgb(253,225,107) , rgb(253,143,3));
                            box-sizing: border-box;
                            padding: 5px 5px;
                            border-radius: 50px;
                            box-shadow: 0px 4px 23px #000;

                            .ActSuc_Item3BckHP{
                                width: 100%;
                                height: 100%;
                                background: linear-gradient(to right , rgb(252,215,38) , rgb(253,174,22));
                                border-radius: 50px;
                                text-align:center;

                                span{
                                    line-height: 37px;
                                    color: #000;
                                    font-size: 18px;
                                    font-weight: bold;
                                }
                            }
                        }
                    }
                }
            }
        }

        //底部标识
        .ActSuc_BottomLogo{
            width: 100%;
            margin-top: 20%;
        }
        //查看订单遮罩层
        .getOrderModel{
            position: fixed;;
            bottom: 0;
            left:0;
            width: 100%;
            height: 100%;
            background:rgba(0,0,0,.91);
        }
        // 查看订单弹窗
        .getOrder_Container{
            background: #fff;
            width:70%;
            height:200px;
            background-color:#fff;
            position: fixed;
            top:50%;
            left:50%;
            transform:translate(-50%,-50%);

            .getOrder_Wrap{
                width: 100%;
                height: 100%;
                box-sizing: border-box;
                padding: 26px 24px;

                .getOrder_Tit{
                    width: 100%;
                    font-size: 20px;
                    
                }
                .getOrder_Main{
                    width: 100%;
                    font-size: 17px;
                    margin-top: 10%;
                    color: rgb(128,128,128);
                }
                .getOrder_Btn{
                    width: 100%;

                    .getOrder_OK , .getOrder_NO{
                        width: 30%;
                        font-size: 17px;
                        text-align: center;
                        float: right;
                        margin-top: 10%;
                    }
                    .getOrder_OK{
                        color: rgb(86, 214, 54);
                    }
                }
            }
        }

        // 二维码遮罩层
        .codeWrap{
            display: none;
            position: fixed;;
            bottom: 0;
            left:0;
            width: 100%;
            height: 100%;
            background:rgba(0,0,0,.91);
            z-index: 999;    
        }
        //二维码样式
        .saveCode_Wrap{
            position: relative;
            width: 100%;
            height: 100%;

            .saveCodeWrap{
                position: fixed;
                top: 0;
                left: 0;
                z-index: 9999;

                .closeCode{
                    display: none;
                    position: fixed;
                    top: 2.6%;
                    right: 4%;

                    span{
                        display: inline-block;
                        width: 30px;
                        height: 30px;
                        // background: url(./images/)
                        background: url(./../Active/images/Act_Suc_Close_item.png) no-repeat center center;
                        background-size: 100%;
                    }
                }

                canvas{
                    width: 70%;
                    margin-left: 55px;
                    border:1px solid #000000;
                    background-color: #000
                }
                #qrcode{
                    width:100px; 
                    height:100px;
                    display: none;
                }
                .codeImg{
                    margin-top: 7%;
                    margin-left: auto;
                    margin-right: auto;
                    // width: 77%;
                    width: 88%;
                    // margin-left: 11.5%;
                    z-index: 9999;
                }
            }
        }
        //保存图片按钮
        .saveCode_BtnWrap{
            display: none;
            position: fixed;
            bottom: 5.5%;
            left: 10%;
            width: 80%;
            height: 60px;
            background: linear-gradient(to bottom , rgb(253,225,107) , rgb(253,143,3));
            box-sizing: border-box;
            padding: 5px 5px;
            border-radius: 50px;
            box-shadow: 0px 4px 23px #000;
            z-index: 999999;


            .saveCode_Btn{
                width: 100%;
                height: 100%;
                background: linear-gradient(to right , rgb(252,215,38) , rgb(253,174,22));
                border-radius: 50px;
                text-align:center;

                span{
                    line-height: 50px;
                    color: #000;
                    font-size: 20px;
                    font-weight: bold;
                }
            }
        }
    }
}