.WOpen_Container{
    width: 100%;
    height: 100%;
    position: fixed;
    top: 0%;
    left: 0%;
    z-index: 99999;
    .WOpen_Wrap{
        width: 100%;
        height: 100%;
        margin-top: 0%;
        margin-left: auto;
        margin-right: auto;
        background: rgba(0,0,0,.9);

        .WOpen_Main{
            width:70%;
            height:200px;
            background-color:#fff;
            position: fixed;
            top:50%;
            left:50%;
            transform:translate(-50%,-50%);
            box-sizing: border-box;
            padding: 26px 24px;

            .getOrder_Tit{
                width: 100%;
                font-size: 20px;
                
            }
            .getOrder_Main{
                width: 100%;
                font-size: 17px;
                margin-top: 10%;
                color: rgb(128,128,128);
            }
        }
    }
}