@import './../../utils/font.scss';
.HPWrap{
    height: 100%;
    .HPMain{
        min-height: 100%;
        background:url(./images/HPBck.jpg) no-repeat top;
        background-size:100% 100%  ;


        //头部样式
        .HP_HeaderWrap{
            width: 100%;
            position: relative;
            overflow: hidden;

            .HP_HeaderTit1{
                width: 100%;
                height: 59px;
                position: relative;
                top:0%;
                font-size: 20px;
                text-align: center;
                line-height: 80px;
                margin-top: 22px;


                .Hp_HeaderTit1Text{
                    color: rgb(252, 217, 119)
                }
                
            }
            .HP_HeaderTit1::before{
                content: '';
                width: 10%;
                height: 3px;
                background: rgb(252, 217, 119);
                position: absolute;
                top: 64%;
                left: 9%;
            }
            .HP_HeaderTit1::after{
                content: '';
                width: 10%;
                height: 3px;
                background: rgb(252, 217, 119);
                position: absolute;
                top: 64%;
                right: 9%;
            }

            .HP_HeaderTit2{
                width: 80%;
                margin: 0 auto;
            }
        }

        //用户信息
        .HPUserInfo_Container{
            width: 100%;
            margin-top: 43px;
            position: relative;

            //头像部分
            .HPUserInfo_TitWrap{
                width: 100%;
                position: relative;

                .HPUserInfo_AvaWrap{
                position: absolute;
                top: -48%;
                left: 42%;
                width: 60px;
                height: 60px;
                background: rgb(253,130,99);
                border-radius: 100%;

                img{
                    width: 82%;
                    margin-left: 6px;
                    margin-top: 5px;
                    border-radius: 100%;
                }
                
            }
                .HPUserInfo_IdWrap{
                    position: absolute;
                    top: 33%;
                    left: 0%;
                    width: 100%;
                    height: 40px;
                    line-height: 40px;
                    text-align: center;


                    span{
                        font-size: 16px;
                        color: #fff;                
                    }
                }

            }
            // 信息部分样式
            .HPUserInfo_MainWrap{
                width: 83%;
                margin: 0 auto;
                display: flex;
                margin-top: -3px;

                //左边框
                .HPUserInfo_Item1{
                    width: 3.4%;
                    height: 100%;
                    
                    img{
                        width:100%;
                        height: 100%;
                    }
                }

                //主内容
                .HPUserInfo_Item2{
                    width: 93.4%;
                    height: 100%;
                    box-sizing: border-box;
                    padding: 18px 10px;

                    //标题
                    .HPUserInfo_MainTit{
                        width: 100%;
                        display: flex;
                        justify-content: space-between;
                        line-height: 28px;

                        .HPUserInfo_MainText{
                            font-size: 13px;
                            color: #fff;
                        }
                        .HPUserInfo_MainBtn{
                            width: 21%;
                            line-height: 28px;
                            border-radius: 50px;
                            background:rgb(252,216,38);
                            color:#000;
                            font-size: 13px;
                            text-align: center;
                        }
                    }

                    //进度条
                    .HPUserInfo_ProGWrap{
                        width: 100%;
                        margin-top: 20px;
                        position: relative;

                        span{
                            display:block;
                            border-radius: 50px;
                        }
                        p{
                            display: inline-block;
                            position: absolute;
                            top: 50%;
                            margin-top: 10px;
                            width: 15%;
                            height: 21px;
                            text-align: center;
                            color: #fff;
                            font-size: 13px;
                        }
                        .HPUserInfo_ProItem1{
                            width: 100%;
                            height: 10px;
                            background-color:rgb(76,64,71);
                        }
                        .HPUserInfo_ProItem2{
                            width: 50%;
                            height: 10px;
                            background-color:rgb(252,216,38);
                        }
                    }

                    //按钮
                    .HPUserInfo_BtnWrap{
                        width: 100%;
                        height: 50px;
                        background: linear-gradient(to bottom , rgb(253,226,108) , rgb(253,143,3));
                        border-radius: 50px;
                        box-sizing: border-box;
                        padding: 5px 10px;
                        margin-top: 32px;

                        .HPUserInfo_BtnLogin{
                            width: 100%;
                            height: 100%;
                            background: linear-gradient(to right , rgb(252,217,99) , rgb(253,174,22));
                            text-align: center;
                            border-radius: 50px;
                            color: #000;
                            line-height: 40px;
                            font-size: 18px;
                            font-weight: bold;
                        }
                    }

                    //无需助力
                    .HPUserInfo_BckPayWrap{
                        width: 60%;
                        margin-top: 17px;
                        margin-left: auto;
                        margin-right: auto;
                        line-height: 13px;
                        color: rgb(253,130,99);
                        text-align: center;
                        font-size: 14px;
                        border-bottom: 1px solid rgb(253,130,99);
                        padding-bottom: 5px;
                        a{
                        color: rgb(253,130,99);
                        }
                        
                    }
                }

                //右边框
                .HPUserInfo_Item3{
                    width: 3.4%;
                    height: 100%;
                }
            }

            //底部
            .HPUserInfo_Bottom{
                width: 100%;
                margin-top: -3px;
            }
        }

        //物品展示
        .HPProduct_Container{
            width: 100%;
            margin-top: 50px;


            //标题部分
            .HPProduct_TitWrap{
                width: 100%;
                height: 100%;
                position: relative;

                span{
                    display: block;
                    position: absolute;
                    top: 31%;
                    left: 0%;
                    width:100%;
                    text-align: center;
                    font-size: 18px;
                    color: #fff;
                    font-weight: bold;
                }

                img{
                    width: 100%;
                    height: 100%;
                }
                .HPProduct_TitItem1 , .HPProduct_TitItem2{
                    position: absolute;
                    top: 39%;
                    width: 8%;

                }
                .HPProduct_TitItem1{
                    left:22%;
                }
                .HPProduct_TitItem2{
                    right: 22%;
                }
            }

            //主内容
            .HPProduct_MainWrap{
                width: 83%;
                height: 150px;
                margin: 0 auto;
                display: flex;
                margin-top: -3px;

                //左边框
                .HPProduct_Item1 , .HPProduct_Item3{
                    width: 4.3%;
                    height: 100%;
                    background: url(./images/HPUserInfo_Bck1.png) no-repeat;
                    background-size: 100% 100%;
                    
                    img{
                        width:100%;
                        height: 100%;
                    }
                }
                //主内容
                .HPProduct_Item2{
                    width: 93.4%;
                    height: 100%;
                    background: url(./images/HPProduct_Img.png) no-repeat center;
                    background-size: 100% 100%;
                }
            }
            //底部
            .HPProduct_Bottom{
                width: 100%;
            }
        }

        // 活动步骤
        .HPActStep_Container{
            width: 87%;
            margin-top: 35px;
            margin-bottom: 10px;
            margin-left: auto;
            margin-right: auto;
            overflow: hidden;

            //标题
            .HPActStep_Header{
                position: relative;
                width: 100%;
                height: 50px;
                line-height: 50px;
                background-color: rgb(81,31,40);
                border-top-left-radius:5px;
                border-top-right-radius:5px;
                border-bottom:3px dashed #000;
                text-align:center;

                span{
                    color:rgb(255,179,146);
                    font-size: 20px;
                    font-weight: bold;
                }

                .HPActStep_HeaderItem1{
                    width: 8%;
                    position: absolute;
                    top: -2%;
                    left: 25%;   
                }
                .HPActStep_HeaderItem2{
                    width: 8%;
                    position: absolute;
                    top: -2%;
                    right: 25%;  
                }
            }

            //用户内容
            .HPActStep_NewUserWrap , .HPActStep_OldUserWrap{
                width: 100%;
                position: relative;
                background:rgb(81,31,40);
                border-bottom:3px dashed #000;
                box-sizing:border-box;
                padding-top:15px;
                color: #fff;

                .CriItem1{
                    position: absolute;
                    top: -5%;
                    left: -2.6%;
                    width: 16px;
                    height: 16px;
                    border-radius: 100%;
                    background-color: #000;;
                }
                .CriItem2{
                    position: absolute;
                    top: -5%;
                    right: -2.6%;
                    width: 16px;
                    height: 16px;
                    border-radius: 100%;
                    background-color: #000;;
                }

                .HPActStep_NewUserTit{
                    width: 52%;
                    height: 27px;
                    background:rgb(115,51,52);
                    text-align:center;
                    margin-left:auto;
                    margin-right:auto;
                    border-radius:50px;
                    line-height:27px;
                    border:2px solid rgb(210, 105, 84);
                    font-size: 15px;
                }

                .HPActStep_NewUserMain{
                    width: 80%;
                    margin-top:15px;
                    margin-bottom:17px;
                    margin-left:auto;
                    margin-right:auto;
                    font-size: 15px;


                    li{
                        margin-bottom: 5px;
                    }
                }
            }
            .HPActStep_OldUserWrap{
                border-bottom:03px dashed transparent;
                border-top-left-radius:5px;
                border-top-right-radius:5px;
            }
        }

        //活动规则
        .HPActRuler_Container{
            width: 87%;
            position: relative;
            margin-top: 25px;
            margin-left: auto;
            margin-right: auto;

            //标题
            .HPActRuler_Header{
                position: relative;
                width: 100%;
                height: 50px;
                line-height: 50px;
                background-color: rgb(81,31,40);
                border-top-left-radius:5px;
                border-top-right-radius:5px;
                border-bottom:3px dashed #000;
                text-align:center;

                span{
                    color:rgb(255,179,146);
                    font-size: 20px;
                    font-weight: bold;
                }

                .HPActRuler_HeaderItem1{
                    width: 8%;
                    position: absolute;
                    top: -2%;
                    left: 25%;   
                }
                .HPActRuler_HeaderItem2{
                    width: 8%;
                    position: absolute;
                    top: -2%;
                    right: 25%;  
                }
            }

            //内容
            .HPActRuler_MainWrap{
                position: relative;
                width: 100%;
                background:rgb(81,31,40);
                border-bottom-left-radius:5px;
                border-bottom-right-radius:5px;
                box-sizing:border-box;
                padding-top:8px;
                padding-bottom:10px;
                color: #fff;

                .HPActRuler_Main{
                    width: 80%;
                    margin-top:15px;
                    margin-bottom:17px;
                    margin-left:auto;
                    margin-right:auto;
                    font-size: 15px;

                    li{
                        margin-bottom:11px;
                    }
                }


            }
        }

        //底部标识  
        .HPBotLogo_Container{
            width: 100%;
            position: relative;
            margin-top: 35px;
        }



        //助力人界面样式


            //助力弹窗遮罩
            .actPopupModle{
                position: fixed;;
                bottom: 0;
                left:0;
                width: 100%;
                height: 100%;
                background:rgba(0,0,0,.91);
            }
            //助力弹窗
            .actPopup_wrap{
                width: 100%;
                position: fixed;
                top: 0%;
                left: 0%;
            }
            .actPopup_wrap .actPopup_Clo{
                width: 12%;
                margin-top: 10%;
                margin-left: auto;
                margin-right: auto;
            }
            .actPopup_Clo img{
                width: 100%;
            }
            .actPopup_main{
                position: relative;
                width: 80%;
                margin-top: 28%;
                margin-left: auto;
                margin-right: auto;
            }
            .actPopup_main img{
                width: 100%;
            }
            .actPopup_main .actPopup_Tit{
                width: 100%;
                position: absolute;
                top: 42%;
                left: 0%;
                text-align: center;
                font-size: 20px;
                font-weight: bold;
            }
            .actPopup_main .actPopup_Info{
                width: 100%;
                position: absolute;
                top: 66%;
                left: 0%;
                text-align: center;
                font-size: 16px;
            }
            .actPopup_main .actPopup_BtnWrap{
                width: 79%;
                height: 43px;
                background: linear-gradient(to bottom , rgb(247,8,156) , rgb(63,191,229));
                position: absolute;
                top: 76%;
                left: 11%;
                border-radius: 50px;
                box-sizing: border-box;
                padding: 5px 5px;
            }
            .actPopup_BtnWrap .actPopup_Btn{
                width: 100%;
                height: 100%;
                background: linear-gradient(to right , rgb(95,90,253) , rgb(248,4,157));
                border-radius: 50px;
                text-align: center;
                line-height: 33px;
                font-size: 18px;
                font-weight: bold;
                color: #fff;
            }

            //助力成功弹窗遮罩
            .actSucModle{
                position: fixed;;
                bottom: 0;
                left:0;
                width: 100%;
                height: 100%;
                background:rgba(0,0,0,.91);
            }

            // 助力成功弹窗
            .actSuc_Wrap{
                width: 100%;
                position: fixed;
                top: 0%;
                left: 0%;
            }
            .actSuc_Wrap .actSuc_Main{
                width: 80%;
                margin-top: 28%;
                position: relative;
                margin-left: auto;
                margin-right: auto;
            }
            .actSuc_Main img{
                width: 100%;
            }
            .actSuc_Main .actSuc_Tit{
                width: 100%;
                position: absolute;
                top: 50%;
                font-size: 28px;
                color: #fff;
                font-weight: bold;
                text-align: center;
            }
            .actSuc_Main .actSuc_Info{
                width: 100%;
                position: absolute;
                top: 65%;
                font-size: 16px;
                color: #fff;
                // font-weight: bold;
                text-align: center;
            }
            .actSuc_Main .actSuc_BtnWrap{
                width: 80%;
                height: 50px;
                position: absolute;
                top: 78%;
                left: 11%;
                background: linear-gradient(to bottom , rgb(253,225,107) , rgb(253,143,3));
                box-sizing: border-box;
                padding: 5px 5px;
                border-radius: 50px;
                box-shadow: 0px 4px 23px #000;

                .actSuc_Btn{
                    width: 100%;
                    height: 100%;
                    background: linear-gradient(to right , rgb(252,215,38) , rgb(253,174,22));
                    border-radius: 50px;
                    text-align:center;

                    span{
                        line-height: 40px;
                        color: #000;
                        font-size: 20px;
                        font-weight: bold;
                    }
                }
            }
            .actSuc_Wrap .actSuc_Clo{
                width: 12%;
                margin-top: 10%;
                margin-left: auto;
                margin-right: auto;
            }
            .actSuc_Clo img{
                width: 100%;
            }
            

        //********************




        //被助力人界面样式
            // 保存图片遮罩层
            .imgWrap{
                position: fixed;;
                bottom: 0;
                left:0;
                width: 100%;
                height: 100%;
                background:rgba(0,0,0,.91);
            }
            // 保存图片
            .saveImgWrap{
                position: fixed;
                bottom: 0;
                left: 0;
                width: 100%;
                height: 112px;
                background:white;
                z-index: 99;
                box-sizing:border-box;
                padding: 0.1rem 0;
                margin:0 auto;

                .saveImg{
                    width: 100%;
                    height: 100%;
                    // background:red;
                    margin:0 auto;
                    // display: flex;
                    img{
                        display: block;
                        margin:13px auto;
                    }

                    span{
                        font-size: 15px;
                        display: block;
                        // width: 20%;
                        margin-top: 10px;
                        text-align: center;
                    }
                }
            }

            // 二维码遮罩层
            .codeWrap{
                display: none;
                position: fixed;;
                bottom: 0;
                left:0;
                width: 100%;
                height: 100%;
                background:rgba(0,0,0,.91);
                z-index: 999;    
            }
            //二维码样式
            .saveCode_Wrap{
                position: relative;
                width: 100%;
                height: 100%;

                .saveCodeWrap{
                    position: fixed;
                    top: 0;
                    left: 0;
                    z-index: 9999;
    
                    .closeCode{
                        display: none;
                        position: fixed;
                        top: 2.6%;
                        right: 4%;
    
                        span{
                            display: inline-block;
                            width: 30px;
                            height: 30px;
                            // background: url(./images/)
                            background: url(./../Active/images/Act_Suc_Close_item.png) no-repeat center center;
                            background-size: 100%;
                        }
                    }
    
                    canvas{
                        width: 70%;
                        margin-left: 55px;
                        border:1px solid #000000;
                        background-color: #000
                    }
                    #qrcode{
                        width:100px; 
                        height:100px;
                        display: none;
                    }
                    .codeImg{
                        margin-top: 7%;
                        margin-left: auto;
                        margin-right: auto;
                        // width: 77%;
                        width: 88%;
                        // margin-left: 11.5%;
                        z-index: 9999;
                    }
                }
            }

            //保存图片按钮
            .saveCode_BtnWrap{
                display: none;
                position: fixed;
                bottom: 5.5%;
                left: 10%;
                width: 80%;
                height: 60px;
                background: linear-gradient(to bottom , rgb(253,225,107) , rgb(253,143,3));
                box-sizing: border-box;
                padding: 5px 5px;
                border-radius: 50px;
                box-shadow: 0px 4px 23px #000;
                z-index: 999999;


                .saveCode_Btn{
                    width: 100%;
                    height: 100%;
                    background: linear-gradient(to right , rgb(252,215,38) , rgb(253,174,22));
                    border-radius: 50px;
                    text-align:center;

                    span{
                        line-height: 50px;
                        color: #000;
                        font-size: 20px;
                        font-weight: bold;
                    }
                }
            }

            //查看订单遮罩层
            .getOrderModel{
                position: fixed;;
                bottom: 0;
                left:0;
                width: 100%;
                height: 100%;
                background:rgba(0,0,0,.91);
            }
            // 查看订单弹窗
            .getOrder_Container{
                background: #fff;
                width:70%;
                height:200px;
                background-color:#fff;
                position: fixed;
                top:50%;
                left:50%;
                transform:translate(-50%,-50%);

                .getOrder_Wrap{
                    width: 100%;
                    height: 100%;
                    box-sizing: border-box;
                    padding: 26px 24px;

                    .getOrder_Tit{
                        width: 100%;
                        font-size: 20px;
                        
                    }
                    .getOrder_Main{
                        width: 100%;
                        font-size: 17px;
                        margin-top: 10%;
                        color: rgb(128,128,128);
                    }
                    .getOrder_Btn{
                        width: 100%;

                        .getOrder_OK , .getOrder_NO{
                            width: 30%;
                            font-size: 17px;
                            text-align: center;
                            float: right;
                            margin-top: 10%;
                        }
                        .getOrder_OK{
                            color: rgb(86, 214, 54);
                        }
                    }
                }
            }
        //********************





        

    }
    
}
@media screen and (max-width : 320px){
    .HPWrap .HPMain .HPUserInfo_Container .HPUserInfo_MainWrap .HPUserInfo_Item2 .HPUserInfo_MainTit .HPUserInfo_MainText{
        font-size: 12px;
    }
    .HPWrap .HPMain .HPUserInfo_Container .HPUserInfo_MainWrap .HPUserInfo_Item2 .HPUserInfo_BckPayWrap{
        width: 72%;
        margin-top: 9px;
    }
    .HPWrap .HPMain .HPUserInfo_Container .HPUserInfo_MainWrap .HPUserInfo_Item2{
        padding: 8px 10px;
    }
}
